import {Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy, TemplateRef} from '@angular/core';

@Component({
  selector: 'cmacs-section',
  templateUrl: 'cmacs-section.component.html',
  styleUrls: ['cmacs-section.component.css'],
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class CmacsSectionComponent implements OnInit {

  @Input() extra: string | TemplateRef<void>;
  @Input() widgetSpan = '24';
  @Input() title = '';
  @Input() titleIcon!: string;
  @Input() showCollapse = true;
  @Input() extraData!: any;

  // collapse
  collapsed = false;
  collapsedIcon = 'iconArrowSmall-Chevron-Down';

  constructor() { }

  ngOnInit() { }

  collapseSection(): void {
    this.collapsed = !this.collapsed;
    this.collapsedIcon = this.collapsed ? 'iconArrowSmall-Chevron-Right' : 'iconArrowSmall-Chevron-Down';
  }
}
